body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


header {
  background-color: #3d3d3d;
}

nav ul {
  list-style: none;
  margin:0;
  padding: 0;
  display: flex;
}

nav ul li {
  padding: 15px 0;
}

nav ul li a {
  color: white;
  font-weight: 500;
  text-decoration: none;
  padding: 15px;
}

a.active {
  background-color: #b59683;
}

.container {
  background-color: #2d2d2d;
  width: 100%;
  min-height: 100vh;
}

.flex {
  display: flex;
}

.character-select-container {
  flex-wrap: wrap-reverse;
  justify-content: center;
}

.character-box {
  background-color: red;
  height: 160px;
  width: 150px;
  margin: 4px 4px;
}

.character-box img {
  height: 100%;
  width: 100%;
 }

.character-box img:hover {
  opacity: 0.7;
  animation-name: hover-opacity;
  animation-duration: .3s;
}

.title {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  color: white;
}

@keyframes hover-opacity {
  from { opacity: 1; }
  to { opacity: 0.7; }
}


.page-404 h2 {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  color: #fff;
}

.home {
  color: #fff;
}

.home h2 {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.about {
  color: #fff;
}

.about h2 {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

